import { Box, Button, Grid, Typography } from "@mui/material";
import DownloadIcon from '@mui/icons-material/Download';
import { ColorString, FontNames, latestDownloadLink } from "../constants";
import LayoutManagerScreenshot from "../img/tutorial/layoutManager.png";
import CreateBrowserSourceScreenshot from "../img/tutorial/createBrowserSource.png";
import BrowserSourcePropertiesScreenshot from "../img/tutorial/browserSourceProperties.png";
import UpdateDataScreenshot from "../img/tutorial/updateData.png";

const DownloadPage = () => {
  const instructionsLeftPadding = '15vw';
  const instructionsVerticalPadding = '30px';

  const instructionGridStyling = { mt: 1 };
  const instructionTitleStyling = { fontFamily: FontNames.MS_BOLDITALIC };
  const instructionContentStyling = {
    fontFamily: FontNames.MS_REGULAR,
    paddingBottom: '15px',
  };
  const instructionImgStyling = {
    width: '100%',
  };
  const instructionContentSubContentStyling = {
    paddingBottom: '4px',
  };

  const textGridSize = 7;
  const imgGridSize = 12 - textGridSize;

  return (
    <>
      <Box
        sx={{
          height: '25vh',
          color: 'text.primary',
          backgroundColor: ColorString.DARK_BLUE,
        }}
      >
        <Box
          sx={{
            paddingTop: '60px',
            display: 'flex',
            justifyContent: 'center',
          }}
        >
          <Typography variant='h4' style={{ fontFamily: FontNames.MS_BOLDITALIC }}>
            Quickstart Instructions
          </Typography>
        </Box>
        <Box
          sx={{
            paddingTop: '10px',
            display: 'flex',
            justifyContent: 'center',
          }}
        >
          <a href={latestDownloadLink}>
            <Button
              variant='contained'
              sx={{ fontFamily: FontNames.MS_BOLDITALIC }}
              startIcon={<DownloadIcon />}
              disabled={true}
            >
              Download StreamWidgets
            </Button>
          </a>
        </Box>
      </Box>
      <Box
        sx={{
          color: 'text.primary',
          backgroundColor: ColorString.PALE_BLUE,
          padding: `${instructionsVerticalPadding} 100px ${instructionsVerticalPadding} ${instructionsLeftPadding}`
        }}
      >
        <Typography variant='h6' style={instructionTitleStyling}>
          1. Download StreamWidgets
        </Typography>
        <Typography variant='body1' style={instructionContentStyling}>
          Download and run the StreamWidgets installer. StreamWidgets should launch automatically, but if not, open it manually.
        </Typography>

        <Grid container spacing={3} sx={instructionGridStyling}>
          <Grid item xs={textGridSize}>
            <Typography variant='h6' style={instructionTitleStyling}>
              2. Create a widget layout
            </Typography>
            <Typography variant='body1' style={instructionContentStyling}>
              Add widgets to your current scene by clicking them in the widget browser. You can move, rename, or change the layering of the widgets you add. Once you are happy with the layout, click the "Save Scene Changes" to save your scene.
            </Typography>
            <Typography variant='body1' style={instructionContentStyling}>
              Note: If you make changes in the "Layout Manager", be sure to save the scene and refresh the browser source in order to see the changes reflected (in OBS, you can click the browser source, and a refresh button should appear below the stream preview window).
            </Typography>
          </Grid>
          <Grid item xs={imgGridSize}>
            <img src={LayoutManagerScreenshot} alt="Layout Manager Screenshot" style={instructionImgStyling}/>
          </Grid>
        </Grid>


        <Grid container spacing={3} sx={instructionGridStyling}>
          <Grid item xs={textGridSize}>
            <Typography variant='h6' style={instructionTitleStyling}>
              3. Add the browser source to your stream
            </Typography>
            <Typography variant='body1' style={instructionContentStyling}>
              In your streaming software of choice, add the browser source (the following instructions use OBS Studio v29.0.2).
            </Typography>
            <Box sx={{ ml: 2 }}>
              <Typography variant='subtitle2' sx={instructionContentSubContentStyling}>
                a. In OBS, ensure you have a scene (if not, add one).
              </Typography>
              <Typography variant='subtitle2' sx={instructionContentSubContentStyling}>
                b. In your scene, add a "Browser" source. You can set the source name as whatever you choose.
              </Typography>
              <Typography variant='subtitle2' sx={instructionContentSubContentStyling}>
                c. Go back to StreamWidgets, and in the scene you just set up, click the "Copy Scene URL" button. This will copy the scene source to your clipboard.
              </Typography>
              <Typography variant='subtitle2' sx={instructionContentSubContentStyling}>
                d. Back in OBS, open the browser properties window (it should be open by default after you created the browser source, but if not, double-click the browser source from your sources list).
              </Typography>
              <Typography variant='subtitle2' sx={instructionContentSubContentStyling}>
                e. Set the browser properties as follows: Uncheck "Local File" (if it is checked), paste the URL in your clipboard in the URL field, set the width and height of the browser source to match your stream output dimensions, (optional) check "Use custom frame rate" and set a custom frame rate (if you have high motion widgets to improve smoothness), (optional) check "Control audio via OBS" if you have widgets that play sound. Click "OK" once everything has been set.
              </Typography>
            </Box>
          </Grid>
          <Grid item xs={imgGridSize}>
            <Grid container>
              <Grid item xs={6}>
                <img src={CreateBrowserSourceScreenshot} alt="OBS Create Browser Source Screenshot" style={instructionImgStyling}/>
              </Grid>
              <Grid item xs={6}>
                <img src={BrowserSourcePropertiesScreenshot} alt="OBS Browser Source Properties Screenshot" style={instructionImgStyling}/>
              </Grid>
            </Grid>
          </Grid>
        </Grid>


        <Grid container spacing={3} sx={instructionGridStyling}>
          <Grid item xs={textGridSize}>
            <Typography variant='h6' style={instructionTitleStyling}>
              4. Update the Widget Data
            </Typography>
            <Typography variant='body1' style={instructionContentStyling}>
              Return to StreamWidgets and switch to the "Manage Widgets" tab. Select the widget you want to update the data of by either clicking it in the layout window, or via the dropdown widget selector. From here, you can freely update the data of this widget by entering new values. Once you are happy with the new values, be sure to click the "Update Widget Data" button to immediately push your changes to your stream output.
            </Typography>
          </Grid>
          <Grid item xs={imgGridSize}>
            <img src={UpdateDataScreenshot} alt="Data Update Panel Screenshot" style={instructionImgStyling}/>
          </Grid>
        </Grid>
      </Box>
    </>
  );
};

export default DownloadPage;
