import { ColorString } from '../../constants';
import { Grid } from '@mui/material';
import ExamplesCard from './ExamplesCard';

// Define the item interface
export interface Item {
  id: number;
  title: string;
  author: string;
  version: string;
  thumbnail: string;
  description: string;
}

interface ExamplesGridProps {
  handleDialogOpenCallback: (item: Item) => void;
}

const ExamplesGrid = (props: ExamplesGridProps) => {
  const { handleDialogOpenCallback } = props;

  // Sample data for the gallery items
  const galleryItems: Item[] = [
    {
      id: 1,
      title: 'Scoreboard',
      author: 'dom',
      version: '0.0.1',
      thumbnail: 'widgetThumbnails/scoreboardThumbnail.png',
      description: 'Ready-to-go scoreboard that pulls data from a text file and live updates whenever the file is updated.',
    },
    {
      id: 2,
      title: 'Bouncing Image',
      author: 'dom',
      version: '0.0.1',
      thumbnail: 'widgetThumbnails/bouncingImage.webm',
      description: 'Easily create a bouncing image. Customize the speed and the image to use in seconds.',
    },
    // Add more items here...
  ];

  return (
    <div className="App" style={{ backgroundColor: ColorString.PALE_BLUE }}>
      <Grid
        container
        spacing={2}
        justifyContent="flex-start"
        direction="row"
        alignItems="flex-start"
      >
        {galleryItems.map((item) => (
          <ExamplesCard item={item} openExamplesDialog={handleDialogOpenCallback} />
        ))}
      </Grid>
    </div>
  );
};

export default ExamplesGrid;
