import {
  Button,
  CardMedia,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
} from '@mui/material';
import { Item } from './ExamplesGrid';

interface ExamplesDialogInterface {
  currItem: Item;
  dialogVisibility: boolean;
  handleDialogClose: () => void;
};

const ExamplesDialog = (props: ExamplesDialogInterface) => {
  const { currItem, dialogVisibility, handleDialogClose } = props;

  return (
    <div>
      <Dialog open={dialogVisibility} onClose={handleDialogClose} aria-labelledby="dialog-title">
        <DialogTitle id="dialog-title">{currItem.title} <span style={{ fontSize: "16px", opacity: "0.7" }}>{`(Version ${currItem.version})`}</span></DialogTitle>
        <DialogContent>
          <div style={{ maxWidth: '100%', marginBottom: '10px' }}>
            {
              currItem.thumbnail.endsWith('.webm') ?
                <CardMedia
                  component="video"
                  autoPlay
                  loop
                  muted
                >
                  <source src={currItem.thumbnail} type="video/webm" />
                  Your browser does not support the video tag.
                </CardMedia> :
                <img src={currItem.thumbnail} alt="Widget Thumbnail"/>
            }
          </div>
          <DialogContentText>
            {currItem.description}
          </DialogContentText>
        </DialogContent>
        <DialogActions style={{ margin: "10px" }}>
          <Button onClick={handleDialogClose} color="primary">
            Cancel
          </Button>
          <Button onClick={handleDialogClose} color="primary" variant="contained">
            Download
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
};

export default ExamplesDialog;
