import Hero from './Hero/Hero';
import FeaturesGrid from './FeaturesGrid';
import GettingStarted from './GettingStarted';
import { ColorString, DivIds } from '../constants';

const LandingPage = () => {
  return (
    <div className="App" style={{ backgroundColor: ColorString.PALE_BLUE }}>
      <div>
        <Hero />
        <div id={DivIds.FEATURES}>
          <FeaturesGrid />
        </div>
        <div id={DivIds.GETTING_STARTED}>
          <GettingStarted />
        </div>
      </div>
    </div>
  );
};

export default LandingPage;
