import { useState } from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import Menu from '@mui/material/Menu';
import MenuIcon from '@mui/icons-material/Menu';
import Container from '@mui/material/Container';
import Button from '@mui/material/Button';
import Tooltip from '@mui/material/Tooltip';
import MenuItem from '@mui/material/MenuItem';
import DownloadIcon from '@mui/icons-material/Download';
import { useNavigate } from 'react-router-dom';
import { ColorString, FontNames, PageToRoutes } from '../constants';
import { navigateToPage } from '../utils';
import StreamWidgetsIcon from '../img/icon.png';

const pages = Object.keys(PageToRoutes);

export default function Navbar() {
  const navigate = useNavigate();
  const [anchorElNav, setAnchorElNav] = useState<null | HTMLElement>(null);

  const handleOpenNavMenu = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorElNav(event.currentTarget);
  };

  const handleCloseNavMenu = () => {
    setAnchorElNav(null);
  };

  const handleNavbarButtonClick = (pageName: string) => {
    navigateToPage(navigate, pageName);
    handleCloseNavMenu();
  };

  return (
    <AppBar
      sx={{ position: 'relative', mb: { xs: 2, md: 0 } }}
      style={{ backgroundColor: ColorString.PALE_BLUE }}
    >
      <Container maxWidth="xl">
        <Toolbar>
          <Box sx={{ display: { xs: 'none', md: 'flex' }, mr: 2 }}>
            <img src={StreamWidgetsIcon} alt='StreamWidgets Icon' />
          </Box>
          <Typography
            variant="h6"
            noWrap
            component="a"
            onClick={() => { navigateToPage(navigate, "Home") }}
            sx={{
              mr: 2,
              display: { xs: 'none', md: 'flex' },
              letterSpacing: '.3rem',
              color: 'inherit',
              textDecoration: 'none',
              cursor: 'pointer',
            }}
          >
            STREAMWIDGETS
          </Typography>

          <Box sx={{ flexGrow: 1, display: { xs: 'flex', md: 'none' } }}>
            <IconButton
              size="large"
              aria-label="account of current user"
              aria-controls="menu-appbar"
              aria-haspopup="true"
              onClick={handleOpenNavMenu}
              color="inherit"
            >
              <MenuIcon />
            </IconButton>
            <Menu
              id="menu-appbar"
              anchorEl={anchorElNav}
              anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'left',
              }}
              keepMounted
              transformOrigin={{
                vertical: 'top',
                horizontal: 'left',
              }}
              open={Boolean(anchorElNav)}
              onClose={handleCloseNavMenu}
              sx={{
                display: { xs: 'block', md: 'none' },
              }}
            >
              {pages.map((page) => (
                <MenuItem key={page} onClick={handleCloseNavMenu}>
                  <Typography textAlign="center">{page}</Typography>
                </MenuItem>
              ))}
            </Menu>
          </Box>
          <Box sx={{ display: { xs: 'flex', md: 'none' }, mr: 2 }}>
            <img src={StreamWidgetsIcon} alt='StreamWidgets Icon' />
          </Box>
          <Typography
            variant="h5"
            noWrap
            component="a"
            onClick={() => { navigateToPage(navigate, "Home") }}
            sx={{
              mr: 2,
              display: { xs: 'flex', md: 'none' },
              flexGrow: 1,
              letterSpacing: '.3rem',
              color: 'inherit',
              textDecoration: 'none',
            }}
          >
            STREAMWIDGETS
          </Typography>
          <Box sx={{ flexGrow: 1, display: { xs: 'none', md: 'flex' } }}>
            {pages.map((page) => (
              <Button
                key={page}
                onClick={() => handleNavbarButtonClick(page)}
                sx={{ my: 2, mr: 2, color: 'white', display: 'block', fontFamily: FontNames.MS_BOLDITALIC }}
              >
                {page}
              </Button>
            ))}
          </Box>
          <Box sx={{ flexGrow: 0, display: { xs: 'none', md: 'flex' } }}>
            <Tooltip title="Download">
              <Button
                variant="outlined"
                startIcon={<DownloadIcon />}
                style={{ fontFamily: FontNames.MS_BOLDITALIC }}
                onClick={() => { navigateToPage(navigate, PageToRoutes.Download) }}
              >
                Download
              </Button>
            </Tooltip>
          </Box>
          <Box sx={{ display: { xs: 'flex', md: 'none' } }}>
            <Tooltip title="Download">
              <IconButton color='primary' onClick={() => { navigateToPage(navigate, PageToRoutes.Download) }}>
                <DownloadIcon />
              </IconButton>
            </Tooltip>
          </Box>
        </Toolbar>
      </Container>
    </AppBar>
  );
};
