import { Box, Button, Container, Grid, Typography } from '@mui/material';
import { scrollDivIntoView } from '../../utils';
import { ColorString, DivIds, FontNames } from '../../constants';
import TransformedSplash from '../../img/TransformedSplash.png';
import './Hero.css';

const Hero = () => {
  return (
    <Box sx={{ height: "75vh", color: 'text.primary', backgroundColor: ColorString.DARK_BLUE }}>
      <Container sx={{ height: "100%" }}>
        <Grid container spacing={3} alignItems="center" sx={{ height: "100%" }}>
          <Grid item xs={12} md={6}>
            <Typography variant='h4' gutterBottom style={{ fontFamily: FontNames.MS_BOLDITALIC }}>
              Take Your Stream to the Next Level in Seconds
            </Typography>
            <Typography variant="subtitle1" gutterBottom>
              From simple widgets to track your win-loss record, to complex custom bouncing DVD logos - StreamWidgets makes adding powerful widgets to your stream fast and easy.
            </Typography>
            <Box sx={{ pt: 2 }}>
              <Button
                variant='contained'
                color='primary'
                style={{fontFamily: FontNames.MS_BLACKITALIC}}
                onClick={() => scrollDivIntoView(DivIds.GETTING_STARTED)}
              >
                Get Started
              </Button>
            </Box>
          </Grid>
          <Grid item xs={12} md={6}>
            <Box sx={{ overflow: "hidden" }}>
              <img src={TransformedSplash} alt="Application Screenshot" style={{ maxWidth: "500px", height: "auto" }} />
            </Box>
          </Grid>
        </Grid>
      </Container>
    </Box>
  );
};

export default Hero;
