import './App.css';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { DivIds, FontNames, RouteNames } from './constants';
import { HashRouter, Route, Routes } from 'react-router-dom';
import Navbar from './components/Navbar';
import Footer from './components/Footer';
import DownloadPage from './components/DownloadPage';
import LandingPage from './components/LandingPage';
import ExamplesPage from './components/Examples/ExamplesPage';

function App() {
  const darkTheme = createTheme({
    palette: {
      mode: 'dark',
      text: {
        primary: '#FFFFFF',
      },
    },
    typography: {
      fontFamily: FontNames.MS_ITALIC,
    },
  });

  return (
    <ThemeProvider theme={darkTheme}>
      <HashRouter>
        <header className="App-header">
          <Navbar />
        </header>
        <Routes>
          <Route path={RouteNames.ROOT} element={<LandingPage />} />
          <Route path={RouteNames.DOWNLOAD} element={<DownloadPage />} />
          <Route path={RouteNames.EXAMPLES} element={<ExamplesPage />} />
        </Routes>
        <div id={DivIds.CONTACT}>
          <Footer />
        </div>
      </HashRouter>
    </ThemeProvider>
  );
}

export default App;
