import { Button, Card, CardActionArea, CardContent, CardMedia, Grid, Typography } from "@mui/material";
import { Item } from "./ExamplesGrid";

interface ItemProps {
  item: Item;
  openExamplesDialog: (item: Item) => void;
};

const ExamplesCard = (props: ItemProps) => {
  const { item, openExamplesDialog } = props;
  const { id, title, author, version, thumbnail, description } = item;

  return (
    <Grid item key={id} xs={12} sm={6} md={4} marginBottom={'40px'}>
      <Card
        sx={{ borderRadius: '20px' }}
        style={{ maxWidth: 345, maxHeight: 320, minHeight: 320 }}
      >
        <CardActionArea onClick={() => openExamplesDialog(item)}>
          {
            thumbnail.endsWith('.webm') ?
              <CardMedia
                component="video"
                autoPlay
                loop
                muted
              >
                <source src={thumbnail} type="video/webm" />
                Your browser does not support the video tag.
              </CardMedia> :
              <CardMedia
                style={{ height: 200 }}
                image={thumbnail}
              />
          }
        </CardActionArea>
        <CardContent>
          <Grid container spacing={1}>
            <Grid item xs={9}>
              <Typography gutterBottom variant="h6" style={{display: 'inline-block', marginRight: '12px'}}>
                {title}
              </Typography>
              <Typography gutterBottom variant="subtitle2" style={{display: 'inline-block', color: 'rgba(255, 255, 255, 0.5)'}}>
                {author}
              </Typography>
              <Typography
                sx={{
                  overflow: "hidden",
                  textOverflow: "ellipsis",
                  display: "-webkit-box",
                  WebkitLineClamp: "2",
                  WebkitBoxOrient: "vertical",
                }}
                variant="body2"
                color="textSecondary"
              >
                {description}
              </Typography>
            </Grid>
            <Grid item xs={3} textAlign="center" style={{ marginTop: "7px" }}>
              <Typography variant="body2" color="textSecondary">
                {version}
              </Typography>
              <Button size="small" color="primary" variant="contained" style={{ marginTop: "5px" }}>
                Get
              </Button>
            </Grid>
          </Grid>
        </CardContent>
      </Card>
    </Grid>
  );
};

export default ExamplesCard;