import { PageToRoutes } from "./constants";

export const scrollDivIntoView = (divId: string) => {
  const section = document.getElementById(divId);
  if (section !== null) {
    section.scrollIntoView({ behavior: 'smooth' });
  }
};

export const navigateToPage = (navigate: any, pageName: string) => {
  const routeName: string = PageToRoutes[pageName];
  navigate(routeName);
  window.scrollTo(0,0);
};

export const navigateToRawPageRoute = (navigate: any, pageRoute: string) => {
  navigate(pageRoute);
  window.scrollTo(0,0);
};