import { Box, Container, Grid, Link, Typography } from '@mui/material';

const Footer = () => {
  return (
    <Box sx={{ bgcolor: '#121212', py: 4 }}>
      <Container maxWidth="lg">
        <Grid container spacing={3}>
          <Grid item xs={12} md={3}>
            <Typography variant="h6" gutterBottom sx={{ color: 'text.secondary', fontWeight: 'bold' }}>
              StreamWidgets
            </Typography>
            <Typography variant="body2" color="text.secondary" gutterBottom>
              Making streaming easier, one widget at a time.
            </Typography>
            <Box sx={{ pt: 2 }}>
              <Typography variant="body2" color="text.secondary">
                 {new Date().getFullYear()} StreamWidgets
              </Typography>
            </Box>
          </Grid>
          <Grid item xs={12} md={3}>
            <Typography variant="h6" gutterBottom sx={{ color: 'text.secondary', fontWeight: 'bold' }}>
              Contact Us
            </Typography>
            <Typography variant="body2" color="text.secondary" gutterBottom>
              Email: streamwidgets@gmail.com
            </Typography>
            <Typography variant="body2" color="text.secondary" gutterBottom>
              <Link href="https://discord.gg/fZQNWm43T4">
                Discord Support Server
              </Link>
            </Typography>
          </Grid>
          <Grid item xs={12} md={3}>
            <Typography variant="h6" gutterBottom sx={{ color: 'text.secondary', fontWeight: 'bold' }}>
              Links
            </Typography>
            <Typography variant="body2" color="text.secondary" gutterBottom>
              <Link href="#" color="text.secondary">Home</Link>
            </Typography>
            <Typography variant="body2" color="text.secondary" gutterBottom>
              <Link href="#" color="text.secondary">About</Link>
            </Typography>
            <Typography variant="body2" color="text.secondary">
              <Link href="#" color="text.secondary">Contact</Link>
            </Typography>
          </Grid>
        </Grid>
      </Container>
    </Box>
  );
};

export default Footer;
