import { Box, Button, Container, Typography } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import { FontNames, PageToRoutes } from '../constants';
import { navigateToRawPageRoute } from '../utils';

const GettingStarted = () => {
  const navigate = useNavigate();

  return (
    <Container maxWidth="md" sx={{ color: 'text.primary' }}>
      <Box sx={{ py: 10, textAlign: 'center' }}>
        <Typography variant="h3" gutterBottom>
          Get Started
        </Typography>
        <Typography variant="body1" gutterBottom>
          See for yourself if StreamWidgets is right for you - give it a try, and feel free to reach out to us if you have any questions! (And if you don't like it, no hard feelings)
        </Typography>
        <Box sx={{ pt: 4 }}>
          <Button
            variant='contained'
            color='primary'
            sx={{ fontFamily: FontNames.MS_BLACKITALIC }}
            onClick={ () => { navigateToRawPageRoute(navigate, PageToRoutes.Download) }}
          >
            Download and Install
          </Button>
        </Box>
      </Box>
    </Container>
  );
};

export default GettingStarted;
