import { Grid, Typography } from '@mui/material';
import { styled } from '@mui/material/styles';
import { ColorString, FontNames } from '../constants';
import DeveloperSupportThumbnail from '../img/features/developerSupport.png';
import FontsThumbnail from '../img/features/fonts.png';
import HotkeysThumbnail from '../img/features/hotkeys.png';
import ManagerPanelThumbnail from '../img/features/managerPanel.png';
import SceneManagementThumbnail from '../img/features/sceneManagement.png';
import WidgetReloadThumbnail from '../img/features/widgetReload.png';

const FeaturesGridContainer = styled(Grid)(({ theme }) => ({
  padding: theme.spacing(4, 0),
  [theme.breakpoints.up('sm')]: {
    maxWidth: '80%',
    margin: '0 auto',
  },
}));

const FeatureGridItem = styled(Grid)(({ theme }) => ({
  textAlign: 'center',
}));

const features = [
  {
    image: WidgetReloadThumbnail,
    title: 'Seamless Widget Loading',
    caption: 'Load new widgets on the fly without needing to restart StreamWidgets.',
  },
  {
    image: ManagerPanelThumbnail,
    title: 'Intuitive Interaction Panel',
    caption: 'Directly interact with and update widget data from an easy-to-use panel.',
  },
  {
    image: HotkeysThumbnail,
    title: 'Global Hotkeys',
    caption: 'Set custom hotkeys or macros to update widget data, even while StreamWidgets is minimized.',
  },
  {
    image: FontsThumbnail,
    title: 'Shared Fonts',
    caption: 'Upload custom fonts to be shared across your widgets to create a custom and consistent design for your streams.',
  },
  {
    image: SceneManagementThumbnail,
    title: 'Multi-Scene Management',
    caption: 'Set up individual widget layouts for multiple scenes to match all of your streaming needs.',
  },
  {
    image: DeveloperSupportThumbnail,
    title: 'Developer Friendly',
    caption: 'Developers can build widgets for StreamWidgets in React, along with any other compatible libraries.',
  },
];

const imgStyling = {
  borderRadius: '30px',
  border: `3px solid ${ColorString.LIGHT_BLUE}`,
  width: '100%',
  height: '100%',
  marginBottom: '15px',
};

const FeaturesGrid = () => {
  return (
    <FeaturesGridContainer container spacing={5} justifyContent="center">
      {features.map((feature, index) => (
        <Grid item xs={12} sm={4} key={index} sx={{ color: 'text.primary' }}>
          <FeatureGridItem>
            <div>
            <img
              src={feature.image}
              alt={feature.title}
              style={{ objectFit: 'cover', ...imgStyling }}
            />
            </div>
            <Typography variant="h5" gutterBottom sx={{fontFamily: FontNames.MS_BOLDITALIC}}>
              {feature.title}
            </Typography>
            <Typography variant="body1" gutterBottom>
              {feature.caption}
            </Typography>
          </FeatureGridItem>
        </Grid>
      ))}
    </FeaturesGridContainer>
  );
};

export default FeaturesGrid;
