import { useState } from "react";
import { Box, Typography } from "@mui/material";
import { ColorString, FontNames } from "../../constants";
import ExamplesGrid, { Item } from './ExamplesGrid';
import ExamplesDialog from "./ExamplesDialog";

const DefaultItem: Item = {
  id: 0,
  title: "",
  author: "",
  version: "",
  thumbnail: "",
  description: ""
}

const ExamplesPage = () => {
  const [ isDialogOpen, setIsDialogOpen ] = useState(false);
  const [ currItem, setCurrItem ] = useState<Item>(DefaultItem);

  const handleDialogOpenCallback = (item: Item) => {
    setCurrItem(item);
    setIsDialogOpen(true);
  };

  return (
    <div>
      <ExamplesDialog
        currItem={currItem}
        dialogVisibility={isDialogOpen}
        handleDialogClose={() => setIsDialogOpen(false)}
      />
      <Box
        sx={{
          height: '15vh',
          color: 'text.primary',
          backgroundColor: ColorString.DARK_BLUE,
        }}
      >
        <Box
          sx={{
            paddingTop: '40px',
            display: 'flex',
            justifyContent: 'center',
          }}
        >
          <Typography variant='h4' style={{ fontFamily: FontNames.MS_BOLDITALIC }}>
            Quickstart Instructions
          </Typography>
        </Box>
      </Box>
      <ExamplesGrid handleDialogOpenCallback={handleDialogOpenCallback} />
    </div>
  );
};

export default ExamplesPage;
